.orderProductItem {
  display: flex;
  align-items: center;
  background-color: var(--background-color);
  color: var(--text-color);
  border: 1px solid var(--border-color);
  padding: 16px;
  max-width: 100%;
}

.thumbnail {
  width: 50px;
  height: 50px;
  object-fit: cover;
  margin-right: 16px;
}

.details {
  display: flex;
  flex-grow: 1;
  flex-flow: row wrap;
  justify-content: space-around;
}

.name {
  flex: 1;
  font-weight: bold;
  white-space: nowrap;
}

.price,
.quantity {
  flex: 1;
  color: var(--secondary-color);
  white-space: nowrap;
}
