.layout {
  color: var(--text-color);
  background-color: var(--background-color);
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.header {
  padding: 5px 40px;
  position: sticky;
  color: var(--text-color);
  background-color: var(--background-color);
  top: 0;
  border-bottom: 1px solid #d8dbdf;
  z-index: 1;
  display: flex;
  justify-content: space-between;
}

.content {
  flex-grow: 1;
  overflow: auto;
}

.item {
  display: flex;
  align-items: center;
  justify-content: center;
}

.rightWrapper {
  display: flex;
}
